import React, { useState } from "react";
import {
  Avatar,
  Button,
  Paper,
  Grid,
  Typography,
  Container,
  TextField,
} from "@material-ui/core";
import Icon from "./icon";
import { GoogleLogin } from "react-google-login";
import useStyles from "./style";
import LockOutlinedIcon from "@material-ui/icons/LockOpenOutlined";
import Input from "./Input";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { signin, signup } from "../../actions/auth";
function Auth() {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [isSignup, setIsSignup] = useState(false);
  let history = useHistory();
  const [showPassword, setShowPassword] = useState(false);

  const initialState = {
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    confirmPassword: "",
  };
  const [fomrData, setFormData] = useState(initialState);

  const handleChange = (e) => {
    setFormData({ ...fomrData, [e.target.name]: e.target.value });
  };

  const handleShowPassword = () => {
    setShowPassword((prevState) => !prevState);
  };

  const switchMode = (e) => {
    e.preventDefault();
    setIsSignup(!isSignup);
    setShowPassword(false);
  };

  const HandleSubmit = (e) => {
    e.preventDefault();
    if (isSignup) {
      dispatch(signup(fomrData, history));
    } else {
      dispatch(signin(fomrData, history));
    }
  };

  const googleSuccess = async (res) => {
    const result = res.profileObj;
    const token = res.tokenId;
    try {
      dispatch({ type: "AUTH", data: { result, token } });
      history.push("/");
    } catch (error) {
      console.log(error);
    }
  };
  const googleFailure = (error) => {
    console.log("Google Sign in was unsuccessful.");
    console.log(error);
  };

  return (
    <Container component="main" maxwidth="xs">
      <Paper className={classes.paper} elevation={3}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography variant="h5">{isSignup ? "Sign Up" : "Sign In"}</Typography>
        <form className={classes.from} onSubmit={HandleSubmit}>
          <Grid container className="test" spacing={2}>
            {isSignup && (
              <React.Fragment>
                <Input
                  name="firstName"
                  label="First Name"
                  handleChange={handleChange}
                  autoFocus
                  half
                />

                <Input
                  name="lastName"
                  label="Last Name"
                  handleChange={handleChange}
                  half
                />
              </React.Fragment>
            )}
            <Input
              name="email"
              label="Email Address"
              handleChange={handleChange}
              type="email"
            />
            <Input
              name="password"
              label="Password"
              handleChange={handleChange}
              type={showPassword ? "text" : "password"}
              handleShowPassword={handleShowPassword}
            />
            {isSignup && (
              <Input
                name="confirmPassword"
                label="Repeat Password"
                handleChange={handleChange}
                type="password"
              />
            )}
          </Grid>

          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            {isSignup ? "Sign Up" : "Sign In"}
          </Button>
          <GoogleLogin
            clientId="420211506123-adthmhdoge7ermp04b74vpeoamugaigr.apps.googleusercontent.com"
            render={(renderProps) => (
              <Button
                color="primary"
                fullWidth
                onClick={renderProps.onClick}
                className={classes.googleButton}
                disabled={renderProps.disabled}
                startIcon={<Icon />}
                variant="contained"
              >
                Google Sign In
              </Button>
            )}
            onSuccess={googleSuccess}
            onFailure={googleFailure}
            cookiePolicy="single_host_origin"
          />
          <Grid container justify="flex-end">
            <Grid item>
              <Button onClick={switchMode}>
                {isSignup
                  ? "Already have an acount? Sign In"
                  : "Don't have an acount? Sign Up"}
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
}

export default Auth;
